/* headersection */
header{
  position: fixed;
  width: 100%;
  z-index: 2;
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 100px;
  padding: 0 1rem;
  background-color: #f7f3f2;
  color: #212121;
}
nav a{
  margin: 0 2rem;
  text-decoration: none;
  color: #212121;
  font-weight: bolder;
  

}

nav a:hover{
  
  border-bottom: 2px solid black;
}


header .nav-btn{
  padding: 5px;
  cursor: pointer;
  background: transparent;
  border: none;
  outline: none;
  color: #212121;
  visibility: hidden;
  opacity: 0;
  font-size: 1.8rem;
}
@media only screen and (max-width:1024px){
  header .nav-btn{
    visibility: visible;
    opacity: 1;
  }
  header nav{
    position: fixed;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    display: flex;
    z-index: 2;
    background-color: #073331;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 1.5rem;
    transition: 1s;
    transform: translateY(-100vh);
  }
  header .responsive_nav{
    transform: none;
  }

  nav .nav-close-btn{
    position: absolute;
    top: 2rem;
    right: 2rem;
    color: aliceblue;

  }
  nav a{
    color: #ffff;
    font-size: 1.5rem;
  }
}
/* Footer section  */
.footer{
  color: white;
  line-height: 18px;
  padding: 25px;
  background: #212121;
}



