/* Home tech line section */

.tech-conatiner {
    max-width: 1400px;
    margin: 2rem auto;
  }
  
  .separator-productpage {
    display: flex;
    align-items: center;
  }
  
  .separator-productpage .line-productpage {
    height: 2px;
    flex: 1;
    background-color:#0480b8;
  }
  
  .separator-productpage h3 {
    padding: 0 2rem;
    font-weight: bolder;
  }
 
  

  /*product line section */

.tech-conatiner-product {
  max-width: 165px;
  margin: 0.5rem auto;
}

.separator-product {
  display: flex;
  align-items: center;
}

.separator-product .line-product {
  height: 2px;
  flex: 1;
  background-color:#0480b8;
}

.separator-product h3 {
  padding: 0 2rem;
  font-weight: bolder;
}
