.card{
  border-radius: 10px;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.7);
  overflow: hidden;
}
.image-bedliners{
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  opacity: 19 black;
  box-shadow: white;
}

/* Home section */

.tech-conatiner-bedliners {
  max-width: 1400px;
  margin: 2rem auto;
}

.separator-bedliners {
  display: flex;
  align-items: center;
}

.separator-bedliners .line-bedliners {
  height: 2px;
  flex: 1;
  background-color:#b00c0c;
}

.separator-bedliners h3 {
  padding: 0 2rem;
  font-weight: bolder;
}